.columns {
	display: grid;
	justify-items: center;
	grid-template-rows: 5% repeat(15, 1fr);
	border-right: 1px solid var(--primary-clr);
}
.cols-titles {
	display: flex;
	justify-content: center;
	width: 100%;
	height: 100%;
	border-bottom: 1px solid var(--primary-clr);
}
.columns:nth-child(5) {
	border: none;
}